chrome.webNavigation.onBeforeNavigate.addListener(details => {
  const url = details.url;
  if (url.includes("/channel/") || url.includes("/@")) {
    const channelPart = url.match(/(\/channel\/[^\/]+|\/@[^\/]+)\/?/);
    if (channelPart && !url.endsWith("/videos")) {
      chrome.tabs.update(details.tabId, { url: `https://www.youtube.com${channelPart[1]}/videos` });
    }
  }
});